<template >
	<b-container>
		<h1>Tilastot</h1>
		<div style="margin-top:20px; margin-bottom:20px">
				<b-link href="#" @click="search(year)">{{ year }} </b-link> |
				<b-link @click="search(year-1)">{{ year - 1 }} | </b-link>
				<b-link @click="search(year-2)">{{ year - 2 }}</b-link>
			</div>
		<b-card :title="current_year">
		  <b-row class="my-1">
			<b-col sm="12">

				<b-table striped :items="stats" :fields="fields">

				</b-table>
			</b-col>
		  </b-row>
		</b-card>




	</b-container>

</template>


<script>

export default {
	name: 'WEStatistics',
	data: function() {
		return {
			year:0,
			current_year: 0,
			stats: [],
			libs: {},
			fields: [
				{key:'count', label: 'lainat'},
				{key:'kirjaston_nimi', label: 'kirjasto'}
			]
			
		}
	},



	methods: {

		search(year) {
			this.current_year = year
			this.error = null
			
			fetch(this.$G.api_url + "/api/tilastot/" + year)
			.then(response => { 
				if(!response.ok) throw(response.status)
				return response.json()
			})
			.then(json => { 
				this.stats = json
			})

			.catch(data => {
				console.log('error' + data)
				this.error = 'virhe: ' + data
			})
		}
		
	},

	mounted() {
		this.year = new Date().getFullYear()
		this.current_year = this.year
		this.search(new Date().getFullYear())
	}
}
</script>
