<template >
	<b-container fluid>
		<h1>Organisaatiot</h1>


		<!-- display search -->
		<b-card title="haku" v-if="!current && mode == ''">
		  <b-row class="my-1">
			<b-col sm="4">
			  <b-form-input @keydown.enter.native="search" v-model="search_term" id="input-default" placeholder="hae nimen alkukirjaimilla"></b-form-input>
			  <b-button @click="search">hae</b-button>
			</b-col>
		  </b-row>
		</b-card>

		<b-button class="float-right" v-if="!current && mode == ''" @click="mode = 'add_new'" variant="primary" style="margin:20px">lis uusi</b-button>

		<!-- display search result -->
		<div v-if="search_result.length !== 0 && !current && mode == ''">
				<b-table :items="search_result" :fields="fields">
					<template v-slot:cell(sahkoposti)="data">
						<b class="text-info">{{ data.value }}</b>
					</template>
					<template v-slot:cell(nimi)="data">
							<b-link :to="'/organisaatiot/' + data.item._id">{{data.value}}</b-link>
					</template>
				</b-table>
		</div>


		<!-- display individual item -->
		<b-card v-if="current && mode != 'edit'" :title="current.nimi" header-bg-variant="dark">

			<table>
				<tr v-for="field in fields" :key="field.key">
					<td style="min-width:200px">{{field.key}}</td>
					<td><b>{{current[field.key]}}</b></td>
				</tr>
			</table>
			<b-icon v-if="mode != 'edit'" @click="mode = 'edit'" icon="pen" class="float-right pointer" variant="primary">muokkaa</b-icon>

		</b-card>


		<!-- edit form -->
		<b-card v-if="current && mode == 'edit'" :title="current.nimi" header-bg-variant="dark">
			<table v-if="mode == 'edit'">
				<tr v-for="field in fields" :key="field.key">
					<td style="min-width:200px">{{field.key}}</td>
					<td><b-form-input v-model="current[field.key]"></b-form-input></td>
				</tr>
			</table>

			<b-container>
				<b-button  @click="saveForm()" class="float-right" variant="primary" style="margin-left:20px">tallenna</b-button>
				<b-button  @click="loadData()" class="float-right" >peru</b-button>
			</b-container>
		</b-card>

		<b-link v-if="current" :to="'/organisaatiot'">takaisin hakuun</b-link>


		<!-- add new item-->
		<b-card v-if="mode == 'add_new'" title="uusi organisaatio" header-bg-variant="dark">
			<table>
				<tr v-for="field in fields" :key="field.key">
					<td style="min-width:200px">{{field.key}}</td>
					<td><b-form-input v-model="new_obj[field.key]"></b-form-input></td>
				</tr>
			</table>
			<b-button  @click="saveForm()" class="float-right" variant="primary" style="margin-left:20px">tallenna</b-button>
			<b-button @click="mode = ''" class="float-right">peru</b-button>
		</b-card>

		<br><br><h2 v-if="current">maksut</h2>
		<b-card v-if="current">
			<b-table :items="requests" :fields="request_fields">
				<template v-slot:cell(tiedot)="data">
						<b-link :to="'/pyynnot/' + data.item._id">{{data.item.teoksen_tiedot}}</b-link>
				</template>
				<template v-slot:cell(tilaaja)="data">
						<b-link :to="'/asiakkaat/' + data.item.tilaaja._id">{{data.value.sukunimi}}, {{data.value.etunimi}}</b-link>
				</template>

			</b-table>
		</b-card>

	</b-container>

</template>


<script>

export default {
	name: 'WEOrganisation',
	data: function() {
		return {
			search_term: '',
			search_result: [],
			current: null,
			requests: [],
			ready: false,
			mode: '',
			new_obj: {},
			fields: [
				{key: 'nimi'},
				{key: 'kustannuspaikka'},
				{key: 'huomio', label:'listieto'}
			],
			request_fields: [

				{key: '_id', label:'tunniste'},
				{key: 'teos_on', label:'tyyppi'},
				{key: 'tiedot'},
				{key: 'tilaaja'},
				{key: 'kasittelija', label: 'ksittelij'},
				{key: 'status'},
				{key: 'maksu'},
				{key: 'maksaja_info'}
			]
		}
	},

	watch: {
		$route(to, from) {
			if(this.$route.params.id) {
				this.loadData()
			} else {
				this.current = null
			}
		}
	},

	methods: {

		saveForm() {
			console.log('saveform')
			if(this.mode == 'add_new') {
				fetch(this.$G.api_url + "/api/organisaatiot", {method:'POST', body: JSON.stringify(this.new_obj)})
				.then(response => { return response.json()})
				.then(json => {
					this.$router.push("/organisaatiot/" + json._id)
				})
				.catch(data => {console.log('error' + data)})
			} else if(this.mode == 'edit') {
				console.log('edit')
				delete this.current._id
				console.log(this.current)
				fetch(this.$G.api_url + "/api/organisaatiot/" + this.$route.params.id, {method:'PUT', body: JSON.stringify(this.current)})
				.then(response => { return response.json()})
				.then(json => {
					this.loadData()
				})
				.catch(data => {console.log('error' + data)})
			}
		},

		editForm() {
			console.log(this.edit)
			console.log('sdf')
			this.edit = !this.edit
		},

		loadRelated() {
			fetch(this.$G.api_url + "/api/pyynnot/?sort=_id&reverse=1&maksaja_varmistettu=" + this.$route.params.id)
			.then(response => { return response.json()})
			.then(json => {
				this.requests = json
				this.ready = true
			})
			.catch(data => {console.log('error' + data)})
		},

		loadData() {
			if(this.$route.params.id) {
				this.mode = ''
				this.ready = false
				this.current = null
				fetch(this.$G.api_url + "/api/organisaatiot/" + this.$route.params.id)
				.then(response => { return response.json()})
				.then(json => {
					this.current = json
					this.loadRelated()
				})
				.catch(data => {console.log('error' + data)})
			}
		},
		async search() {
			this.current = null
			if(this.search_term !== '') {
				if(!this.search_term.includes('*')) this.search_term = this.search_term + '*'
				var search = 'nimi=' + this.search_term

				var result = await fetch(this.$G.api_url + "/api/organisaatiot?" + search)
				this.search_result = await result.json()
			}
		}
	},

	mounted() {
		this.loadData()
	}
}
</script>
