<template >
	<b-container fluid>
		<h1>Asiakkaat</h1>

		<div v-if="!ready" class="alert alert-info">lataan...</div>

		<!-- display search -->
		<b-card title="haku" v-if="!current_client">
		  <b-row class="my-1">
			<b-col sm="4">
			  <b-form-input @keydown.enter.native="search" v-model="search_term" id="input-default" placeholder="hae sukunimell tai shkpostilla"></b-form-input>
			  <b-button @click="search">hae</b-button>
			</b-col>
		  </b-row>
		</b-card>

		<!-- display search result -->
		<div v-if="search_result.length !== 0 && !current_client">
				<b-table :items="search_result" :fields="fields">
					<template v-slot:cell(sahkoposti)="data">
						<b class="text-info">{{ data.value.join(', ') }}</b>
					</template>
					<template v-slot:cell(sukunimi)="data">
							<b-link :to="'/asiakkaat/' + data.item._id">{{data.value}}</b-link>
					</template>
				</b-table>
		</div>

		<!-- display individual item -->
		<div v-if="current_client">

			<b-card v-if="!edit" :title="current_client.sukunimi + ', ' + current_client.etunimi" header-bg-variant="dark">

				<table>
					<tr v-for="field in fields" :key="field.key">
						<td style="min-width:200px">{{field.label}}</td>
						<td v-if="field.key == 'sahkoposti'" style="min-width:350px" >

							<div v-for="s in current_client.sahkoposti" :key="s">
								<a :href="'mailto:' + s">{{s}}</a>
							</div>
						</td>
						<td v-else><b>{{current_client[field.key]}}</b></td>

					</tr>
				</table>

				<b-icon v-if="!edit" @click="edit = !edit" icon="pen" class="float-right pointer" variant="primary">muokkaa</b-icon>

			</b-card>

		<!-- edit form -->
		<b-card v-if="edit" :title="current_client.sukunimi" header-bg-variant="dark">
			<table class="table table-striped">
				<tr v-for="field in fields" :key="field.key">
					<td style="min-width:200px">{{field.label}}</td>
					<td v-if="field.key == 'sahkoposti'" style="min-width:350px" >
						<b-form-input v-if="current_client[field.key] && current_client[field.key][0]" v-model="current_client[field.key][0]"></b-form-input>
						<b-form-input v-if="current_client[field.key]" v-model="current_client[field.key][1]"></b-form-input>
					</td>
					<td v-else-if="field.key == 'ulkopuolinen'">
						<b-form-checkbox
						  id="checkbox-1"
						  v-model="current_client.ulkopuolinen"
						  name="checkbox-1"
						>
						</b-form-checkbox>
					</td>
					<td v-else style="min-width:350px" ><b-form-input v-model="current_client[field.key]"></b-form-input></td>
				</tr>
			</table>

			<b-container>


			<b-button  @click="saveForm()" class="float-right" variant="primary" style="margin-left:20px">tallenna</b-button>
			<b-button  @click="edit = !edit" class="float-right" >peru</b-button>
			</b-container>
		</b-card>


			<b-link :to="'/asiakkaat'">takaisin hakuun</b-link>

			<h2 style="margin-top:40px">{{laina_title}}</h2>
			<b-form-checkbox
				  id="checkbox-1"
				  v-model="show_all_requests"
				  name="show-all-requests"
				>
				  nyt kaikki pyynnt
			</b-form-checkbox>
			<b-table :items="requests" :fields="request_fields">

				<template v-slot:cell(tiedot)="data">
					<div style="min-width:250px">
						<b-link :to="'/pyynnot/' + data.item._id">{{data.item.teoksen_tiedot}}</b-link>
					</div>
				</template>

				<template v-slot:cell(kirjasto)="data">
					<b-link v-if="data.item.tilaus_saatu" :to="'/kirjastot/' + data.item.tilaus_saatu.kirjasto_id">{{data.item.tilaus_saatu.kirjaston_nimi}}</b-link>

				</template>

				<template v-slot:cell(tilaus_saatu.pvm_erapaiva)="data">
					<DueDate :item="data.item"></DueDate>
				</template>

				<template v-slot:cell(_id)="data">
					{{data.value}}
				</template>
			</b-table>

		</div>

	</b-container>

</template>


<script>
import DueDate from './WEDueDateDisplay.vue'
export default {
	name: 'WEClient',
	components: {DueDate},
	data: function() {
		return {
			search_term: '',
			search_result: [],
			requests: [],
			all_requests: [],
			show_all_requests: false,
			current_client: null,
			ready: true,
			edit: false,
			laina_title: 'lainassa olevat kaukolainat',
			laina_button_title: 'nyt kaikki kaukolainapyynnt',
			fields: [
				{key: 'sukunimi', label:'sukunimi'},
				{key: 'etunimi', label:'etunimi'},
				{key: 'sahkoposti', label:'shkposti'},
				{key: 'kirjastokortin_numero', label:'kirjastokortti'},
				{key: 'puhelin', label:'puhelin'},
				{key: 'osoite', label:'osoite'},
				{key: 'huomio', label:'listieto'},
				{key: 'ulkopuolinen', label:'ulkopuolinen asiakas'}
			],
			request_fields: [
				{key: '_id', label: 'tunniste'},
				{key: 'teos_on', label: 'tyyppi'},
				{key: 'tiedot'},
				{key: 'kirjasto', label: 'kirjasto'},
				{key: 'maksu', label: 'maksu'},
				{key: 'maksaja', label: 'maksaja'},
				{key: 'status'},
				{key: 'tilaus_saatu.pvm_erapaiva', label: 'erpiv'},
			]
		}
	},

	watch: {
		show_all_requests: function() {
			this.showAllRequests()
		},
		// because of <keep-alive>, route changes must check that we are in correct path
		$route(to, from) {
			if(to.path.includes('/asiakkaat')) {
				if(this.$route.params.id) {
					this.loadData()
					// if we hit back button *while in* client view, then we reset current client
				} else if(from.path.includes('/asiakkaat')) {
					this.current_client = null
					// if we came from somewhere else with current set, then re-route so that URL matches current id
				} else {
					if(this.current_client) this.$router.replace({path:'/asiakkaat/' + this.current_client._id})
				}
			}
		}
	},

	methods: {
		saveForm() {
			delete this.current_client._id // do not try to re-save id
			this.current_client.sahkoposti = this.current_client.sahkoposti.filter(x => x !='') // delete empty emails
			fetch(this.$G.api_url + "/api/asiakkaat/" + this.$route.params.id, {method:'PUT', body: JSON.stringify(this.current_client)})
			.then(response => { return response.json()})
			.then(json => {
				this.loadData()
			})
			.catch(data => {
				alert('Talletus ei onnistunut' + data)
				console.log('error' + data)
			})
		},
		loadData() {
			if(this.$route.params.id) {
				this.ready = false
				this.edit = false
				this.current_client = null
				fetch(this.$G.api_url + "/api/asiakkaat/" + this.$route.params.id)
				.then(response => { return response.json()})
				.then(json => {
					this.current_client = json
					this.loadRelated()
				})
				.catch(data => {console.log('error' + data)})
			}
		},

		showAllRequests() {
			if(this.show_all_requests) {
				this.requests = this.all_requests
				this.laina_title = 'kaikki kaukolainapyynnt'
				this.laina_button_title = 'nyt vain lainassa olevat'
			} else {
				this.requests = this.sortByStatus(this.all_requests)
				this.laina_title = 'lainassa olevat kaukolainat'
				this.laina_button_title = 'nyt kaikki kaukalainapyynnt'
			}
		},

		sortByStatus(items) {
			var out = []
			for(var item of items) {
				if(item.status === 'lainassa') out.push(item)
			}
			return out
		},

		loadRelated() {
			fetch(this.$G.api_url + "/api/pyynnot/?sort=tilaus_saatu.pvm_erapaiva&reverse=1&tilaaja=" + this.current_client._id)
			.then(response => { return response.json()})
			.then(json => {
				this.all_requests = json
				this.requests = this.sortByStatus(json)
				this.ready = true
			})
			.catch(data => {console.log('error' + data)})
		},

		async search() {
			this.current_client= null
			if(this.search_term !== '') {
				//if(!this.search_term.includes('*')) this.search_term = this.search_term + '*'
				var search = 'search=' + this.search_term
				//if(this.search_term.includes('@')) search = 'sahkoposti=' + this.search_term

				var result = await fetch(this.$G.api_url + "/api/asiakkaat?" + search)
				this.search_result = await result.json()
			}
		}
	},

	mounted() {
		this.loadData()
	}
}
</script>
