<template >
	<b-container fluid>
		<h1>Saatteet</h1>

		<!-- <b-button class="float-right" v-if="!current && mode == ''" @click="mode = 'add_new'" variant="info">lis uusi</b-button> -->

		<!-- display search result -->
		<div v-if="ready && search_result.length !== 0 && !current && mode == ''">
				<b-table :items="search_result" :fields="fields">

					<template v-slot:cell(_id)="data">
							<b-link :to="'/saatteet/' + data.item._id">{{data.value}}</b-link>
					</template>
				</b-table>
		</div>


		<!-- display individual item -->
		<b-card v-if="ready && current" :title="current._id" header-bg-variant="dark">

			<div style="max-width:700px" v-html="$G.renderBreaks(current.saate,'\n')"></div>
			<br><br>
			<b-button v-if="mode != 'edit'" @click="mode = 'edit'">muokkaa</b-button>

		</b-card>



		<!-- edit form -->
		<b-card v-if="current && mode == 'edit'" :title="current._id" header-bg-variant="dark">
			<table v-if="mode == 'edit'">
				<tr>

					<td style="min-width:200px">saate</td>
					<td><textarea cols="80" rows="16" v-model="current['saate']"></textarea></td>
				</tr>
			</table>

			<b-container>


			<b-button  @click="loadData()">peru</b-button>
			<b-button  @click="saveForm()" variant="info">tallenna</b-button>
			</b-container>
		</b-card>

			<div class="alert alert-info">Nill placeholdereilla voit mritt mihin mikin tieto tulee saatteessa.
				<ul>
					<li>[[ID]] = pyynnn tunniste</li>
					<li>[[FI]] = teoksen tiedot suomeksi </li>
					<li>[[EN]] = teoksen tiedot englanniksi </li>
					<li>[[ERAPAIVA]] = erpiv (saapumisilmoituksessa) </li>
					<li>[[TARVITAAN_VIIMEISTAAN]] = tarvitaan viimeistn (tilauksessa) </li>
				</ul>
			</div>

		<b-link v-if="current" :to="'/saatteet'">takaisin</b-link>


		<!-- add new item-->
		<b-card v-if="mode == 'add_new'" title="uusi saate" header-bg-variant="dark">
			<table>
				<tr v-for="field in fields" :key="field.key">
					<td style="min-width:200px">{{field.key}}</td>
					<td><b-form-input v-model="new_obj[field.key]"></b-form-input></td>
				</tr>
			</table>
			<b-button @click="mode = ''">peru</b-button>
			<b-button @click="saveForm()" variant="info">tallenna</b-button>
		</b-card>

	</b-container>

</template>


<script>

export default {
	name: 'WEMessage',
	data: function() {
		return {
			search_result: [],
			current: null,
			ready: false,
			mode: '',
			new_obj: {},
			fields: [
				{key: '_id'},
				{key: 'saate'}
			],
		}
	},

	watch: {
		$route(to, from) {
			if(this.$route.params.id) {
				this.loadData()
			} else {
				this.current = null
				this.loadData()
			}
		}
	},

	methods: {

		saveForm() {
			if(this.mode == 'add_new') {
				fetch(this.$G.api_url + "/api/saatteet", {method:'POST', body: JSON.stringify(this.new_obj)})
				.then(response => { return response.json()})
				.then(json => {
					this.$router.push("/saatteet/" + json._id)
				})
				.catch(data => {console.log('error' + data)})
			} else if(this.mode == 'edit') {
				console.log('edit')
				delete this.current._id
				console.log(this.current)
				fetch(this.$G.api_url + "/api/saatteet/" + this.$route.params.id, {method:'PUT', body: JSON.stringify(this.current)})
				.then(response => { return response.json()})
				.then(json => {
					this.loadData()
				})
				.catch(data => {console.log('error' + data)})
			}
		},

		editForm() {
			console.log(this.edit)
			console.log('sdf')
			this.edit = !this.edit
		},



		loadData() {

			if(this.$route.params.id) {
				this.mode = ''
				this.ready = false
				this.current = null
				fetch(this.$G.api_url + "/api/saatteet/" + this.$route.params.id)
				.then(response => { return response.json()})
				.then(json => {
					this.current = json
					this.ready = true
				})
				.catch(data => {console.log('error' + data)})
			} else {
				fetch(this.$G.api_url + "/api/saatteet?sort=_id")
				.then(response => { return response.json()})
				.then(json => {
					this.search_result = json
					this.ready = true
				})
				.catch(data => {console.log('error' + data)})
			}
		}
	},

	mounted() {
		this.loadData()
	}
}
</script>
