<template >
	<b-container fluid>
		<h1>Lainat</h1>
		<div class="alert alert-info">lainassa tll hetkell: <b>{{loan_count}}</b></div>
		<div>
			<b-tabs content-class="mt-3" @activate-tab="getOverdueLoans()">
				<b-tab title="lainahaku" active>
					<b-row class="my-1">
						<b-col sm="4">
							<b-form-input @keydown.enter.native="search" v-model="search_term" id="input-default" placeholder="hakutermi"></b-form-input>
							<b-button @click="search">hae</b-button>
							<div class="alert alert-danger" v-if="error">{{error}}</div>
						</b-col>
						<b-col>
							<b-form-select v-model="status" :options="statuses" @change="search"></b-form-select>

						</b-col>
						<b-col>
							<div class="alert alert-info">lainahaku hakee teoksen tiedoista. Tietyn henkiln lainat lytyy asiakashaun kautta.</div>
						</b-col>
					</b-row>
					<b-row>
						<div v-if="search_result.length != 0">
							<b-table small striped  hover :items="search_result" :fields="fields" >
								<template v-slot:cell(teoksen_tiedot)="data">
									<b-link :to="{ path: '/pyynnot/' + data.item._id }">{{data.item.teoksen_tiedot}}</b-link>
								</template>
								<template v-slot:cell(tilaaja)="data">
									<b-link v-if="data.item.tilaaja" :to="{ path: '/asiakkaat/' + data.item.tilaaja._id }">{{ data.item.tilaaja.sukunimi}}, {{data.item.tilaaja.etunimi}}</b-link>
								</template>
								<template v-slot:cell(tilaus_saatu.kirjaston_nimi)="data">
									<div><b-link v-if="data.item.tilaus_saatu" :to="'/kirjastot/' + data.item.tilaus_saatu.kirjasto_id">{{data.value}}</b-link></div>
								</template>
								<template v-slot:cell(tilaus_saatu.pvm_erapaiva)="data">
									<DueDate :item="data.item"></DueDate>
								</template>
							</b-table>
						</div>
						<div v-else>ei tuloksia</div>
					</b-row>

				</b-tab>

				<b-tab title="erntyneet">
					<template v-if="overdue.length != 0" >
						<div>erntyneit lainoja {{overdue.length}}</div>
						<b-table small striped  hover :items="overdue" :fields="fields" >
							<template v-slot:cell(teoksen_tiedot)="data">
								<b-link :to="{ path: '/pyynnot/' + data.item._id }">{{data.value}}</b-link>
							</template>
							<template v-slot:cell(tilaaja)="data">
								<div><b-link v-if="data.item.tilaaja" :to="'/asiakkaat/' + data.value._id">{{data.value.etunimi}} {{data.value.sukunimi}} </b-link></div>
							</template>
							<template v-slot:cell(tilaus_saatu.pvm_erapaiva)="data">
								<DueDate :item="data.item"></DueDate>
							</template>
						</b-table>
					</template>
				</b-tab>
			</b-tabs>
		</div>




	</b-container>

</template>


<script>
import axios from 'axios'
import DueDate from './WEDueDateDisplay.vue'
import web from './../web.js'
export default {
	name: 'WELoans',
	components: {DueDate},
	data: function() {
		return {
			search_result: [],
			search_term: '',
			current: null,
			loading: false,
			error: null,
			overdue: [],
			loan_count: 0,
			status: 'lainassa',
			statuses: ['ei valintaa','kasittelyssa', 'lainassa', 'tilattu', 'arkistoitu', 'peruttu'],
			fields: [
				{key:'_id', label: 'tunnus'},
				{key:'teoksen_tiedot', label: 'kirjan nimi'},
				{key:'tilaaja', label: 'tilaaja'},
				{key:'tilaus_saatu.kirjaston_nimi', label: 'kirjasto'},
				{key:'status', label: 'status'},
				{key:'tilaus_saatu.pvm_erapaiva', label: 'erpiv', sortable:true}

			]
		}
	},



	methods: {

		async getOverdueLoans() {
			var response = await axios(this.$G.api_url + "/api/pyynnot/overdue_loans")
			this.overdue = response.data
		},

		search() {
			if(this.search_term == '') {
				this.error = "anna hakuehto"
				return
			}
			this.error = null
			var query = ''
			if(this.status == 'ei valintaa')
				query = "?search=" + this.search_term
			else
				query = "?search=" + this.search_term + "&status=" + this.status + "&sort=tilaus_saatu.pvm_erapaiva&reverse=1"

			fetch(this.$G.api_url + "/api/pyynnot" + query)
			.then(response => {
				if(!response.ok) throw(response.status)
				return response.json()
			})
			.then(json => {
				this.search_result = json
				this.ready = true
			})
			.catch(data => {
				console.log('error' + data)
				this.error = 'virhe: ' + data
			})
		}
	},

	async mounted() {
		 var count = await web.getLoanCount()
		 this.loan_count = count.data
	}
}
</script>
