<style scoped>
	.cursor {
		cursor: pointer;
	}
	.card-body {
		#padding: 0px
	}
	.card-title {
		font-style: italic;
	}
	.card-header {
		border-color: #002957;
		border-width: 5px;
		background-color: white;
	}
	.text-danger {
		font-weight: bold;
	}
	.dim {
		background-color: #f1f1f2;
	}
</style>

<template >
	<b-container>
		<div v-if="ready">

			<b-card header="kaukolainapyynt">
				<template #header>
					<b-nav class="float-right">
	  				    <b-nav-item-dropdown
	  				      id="my-nav-dropdown"
	  				      text="toiminnot"
	  				      toggle-class="nav-link-custom"
	  				      right
	  				    >

						<b-dropdown-item v-if="request.status == 'lainassa'" class="float-right"  @click="setReturned()"><b>merkitse palautetuksi ja arkistoi</b></b-dropdown-item>
						  <b-dropdown-item v-if="request.tilaus_saatu && request.tilaus_saatu.status == 'lainassa'" @click="openKarhuRequest()" >lhet karhuviesti</b-dropdown-item>
	  				      <b-dropdown-item v-if="request.status == 'kasittelyssa' || request.status == 'tilattu' || request.status == 'uusi'" @click="show = !show" ><b>tee uusi tilaus</b></b-dropdown-item>

	  				      <b-dropdown-divider></b-dropdown-divider>
	  				      <b-dropdown-item v-if="(request.status == 'kasittelyssa' || request.status == 'tilattu') && request.teos_on == 'artikkeli'" @click="convertToBook()">muuta kirjapyynnksi</b-dropdown-item>
						  <b-dropdown-item  @click="show_modal_cancel_archive = true" v-if="request.status === 'peruttu' && !request.tilaus_saatu">palauta ksittelyyn</b-dropdown-item >
	  					  <b-dropdown-item v-if="request.status != 'peruttu' && !request.tilaus_saatu" @click="openCancelRequest()">peruuta pyynt</b-dropdown-item>
	  				    </b-nav-item-dropdown>
	  				  </b-nav>
					<h3 >{{request.teos_on}}pyynt: <b>{{request._id}}</b> (<span class="text-info">{{$G.getStatus(request.status)}}</span>)</h3>
				</template>

				<b-card-body>
					<b-card-title>{{request.teoksen_tiedot}} <b-icon v-if="request.status != 'arkistoitu'" icon="pen" variant="primary" id="edit-note" class="float-right cursor" @click="show_modal_title = true"></b-icon></b-card-title>

				<table class="table">
					<tbody>
						<tr v-if="request.tilaaja">
							<td style="width:200px">asiakas</td>
							<td>
								<b><b-link :to="'/asiakkaat/' + request.tilaaja._id">{{request.tilaaja.etunimi}} {{request.tilaaja.sukunimi}} </b-link></b>
								<b-link v-if="!request.tilaaja.sukunimi || request.tilaaja.sukunimi == ''" :to="'/asiakkaat/' + request.tilaaja._id">nimi puuttuu </b-link>

								<div v-if="isExternal()">
									<p class="alert alert-warning">Kaukolainapyynt on tehty kaukolainahenkilkunnan tunnuksilla, joten asiakas tytyy liitt pyyntn ksin (ja mahdollisesti list shkposti) tai luoda uusi asiakas.</p>
									<p><b>{{request.sukunimi}}, {{request.etunimi}}</b></p>
									<p><b-input @keydown.enter.native="searchClients()" v-model="client_query"></b-input>
										<b-button @click="searchClients()">hae</b-button>
									</p>

									<template v-if="clients_result && clients_result.length">
										<b-table small striped hover :items="clients_result" :fields="client_fields">
											<template v-slot:cell(sahkoposti)="data">
												{{data.value.join(', ')}}
											</template>
											<template v-slot:cell(valitse)="data">
												<b-button title="liit asiakas ja anna tarvittaessa toinen shkposti" variant="primary" @click="openJoinClient(data.item)">liit</b-button>
											</template>
										</b-table>
									</template>
									<div v-else>ei tuloksia</div>

									<b-button class="float-right" style="margin-top:40px" variant="primary" @click="show_modal_external = !show_modal_external">Luo uusi asiakas</b-button>

								</div>
							</td>
							<td>

							</td>
						</tr>

						<tr v-else>
							<td>asiakas</td>
							<td>
								<div v-if="isExternal()">
									<p><b>{{request.sukunimi}}, {{request.etunimi}}</b></p>
									<p class="alert alert-warning">Kaukolainapyynt on tehty kaukolainahenkilkunnan tunnuksilla, joten asiakas tytyy liitt pyyntn ksin (ja mahdollisesti list shkposti) tai luoda uusi asiakas.</p>
									<p><b-input @keydown.enter.native="searchClients()" v-model="client_query"></b-input>
										<b-button @click="searchClients()">hae</b-button>
									</p>

									<template v-if="clients_result && clients_result.length">
										<b-table small striped hover :items="clients_result" :fields="client_fields">
											<template v-slot:cell(sahkoposti)="data">
												{{data.value.join(', ')}}
											</template>
											<template v-slot:cell(valitse)="data">
												<b-button title="liit asiakas ja anna tarvittaessa toinen shkposti" variant="primary" @click="openJoinClient(data.item)">liit</b-button>
											</template>
										</b-table>
									</template>
									<div v-else>ei tuloksia</div>

									<b-button class="float-right" style="margin-top:40px" variant="primary" @click="show_modal_external = !show_modal_external">Luo uusi asiakas</b-button>

								</div>
							</td>
							<td></td>
						</tr>
<!--
						<tr v-if="request.status == 'lainassa'">
							<td>kirjastosta</td>
							<td>
								<b-link  :to="'/kirjastot/' + request.lainassa_kirjastosta">{{loan_library_name}}</b-link>
							</td>
							<td></td>
						</tr>

						<tr v-if="request.status == 'lainassa' && request.tilaus_saatu">
							<td>erpiv</td>
							<td>
								<b>{{formatDate(request.tilaus_saatu.pvm_erapaiva, 'reverse','.')}} </b>
							</td>
							<td>
								<b-button class="float-right" variant="primary" @click="show_modal_loan_date = true" size="sm">muuta erpiv</b-button><br><br>
							</td>
						</tr>

					-->

						<template v-if="request.teos_on == 'artikkeli'">
							<tr>
								<td style="min-width:200px">mist lysit?</td>
								<td><b>{{request.mista_loysit_julkaisun_tiedot}}</b></td>
								<td></td>
							</tr>
						</template>

						<tr >
							<td>
								maksaja
							</td>
							<td>
								<b-badge v-if="request.hintakatto" class="float-right alert alert-info">hintakatto: {{request.hintakatto}}</b-badge>
								<div v-if="!request.maksaja_varmistettu && request.maksaja != 'asiakas'" class="alert alert-warning">maksajaa ei ole liitetty mihinkn organisaatioon</div>
								<div v-if="request.maksaja == 'asiakas'"><b>asiakas itse</b></div>
								<div v-else-if="!request.maksaja_varmistettu">
									<b v-if="!request.maksaja_varmistettu"> {{request.maksaja}}: {{request.laitos}} {{request.projekti}}</b>

								</div>
								<div v-else>
									<span title="lomakkeelta tullut maksajatieto">{{request.laitos}}: </span>
									<b-link title="linkitetty maksajatieto" :to="'/organisaatiot/' + request.maksaja_varmistettu._id">{{request.maksaja_varmistettu.nimi}}</b-link>
								</div>
								<div v-if="request.maksaja_varmistettu">
									<table class="table table-striped">
										<tbody>
											<tr v-if="request.projekti"><td>projekti:</td><td><b> {{request.projekti}}</b></td></tr>
											<tr v-if="request.kustannuspaikka"><td>kustannuspaikka:</td><td><b> {{request.kustannuspaikka}}</b></td></tr>
										</tbody>
									</table>

									<div v-if="request.maksaja_info && request.maksaja_varmistettu" class="alert alert-info">{{request.maksaja_info}}</div>

								</div>


							</td>
							<td title = "muokkaa maksajatietoja"><b-icon v-if="request.status != 'arkistoitu'" icon="pen" variant="primary" id="edit-note" class="float-right cursor" @click="show_modal_payer = true"></b-icon></td>
						</tr>
<!--
						<tr>
							<td style="min-width:200px">maksu</td>
							<td>
								{{request.maksu}} (tieto tulee tilauksesta)
							</td>
							<td></td>
						</tr>
-->
						<tr>
							<td style="min-width:200px">huomautuksia <br>(sisinen)</td>
							<td>
								<div v-if="request.huomautuksia != ''" class="alert alert-info" v-html="$G.renderBreaks(request.huomautuksia,'\n')"></div>
							</td>
							<td title = "muokkaa huomautuksia"><b-icon icon="pen" variant="primary" id="edit-note" class="float-right cursor" @click="show_modal_note = !show_modal_note"></b-icon></td>
						</tr>
					</tbody>
				</table>
				<div v-if="request.tarvitaan_viimeistaan" class="alert alert-info">tarvitaan viimeistn: {{request.tarvitaan_viimeistaan}}</div>
				<div v-if="request.o_id" class="alert alert-danger">Vanha laina Kakesta!</div>
				<div v-if="request.peruutuksen_syy">peruutuksen syy: <b class="text-danger">{{request.peruutuksen_syy}} </b> </div>
				<div v-if="request.o_retudate_has_value == 'yes'" class="alert alert-info" >Tm lienee palautettu {{request.pvm_palautettu}}?</div>

				<div v-if="request.karhut && request.karhut.length">
					<div v-for="karhu in request.karhut" :key="karhu" class="alert alert-danger">
						karhuttu: {{karhu}}
					</div>
				</div>
			</b-card-body>
		</b-card>
		<br>


		<b-card v-if="(request.status == 'lainassa' || request.status == 'arkistoitu') && request.tilaus_saatu" :footer="'palautettu: ' + formatDate(request.tilaus_saatu.pvm_palautettu,'reverse', '.')" header-class="dim" header-text-variant="white">
			<template #header >

			  <h6 class="mb-0"><b>kaukolaina</b> (saapunut: {{formatDate(request.tilaus_saatu.pvm_saapunut, 'reverse', '.')}})</h6>

	        </template>
			<table class="table">
				<tr>
					<td>kirjastosta</td>
					<td><b-link text-variant="white" :to="'/kirjastot/' + request.tilaus_saatu.kirjasto_id"> {{request.tilaus_saatu.kirjaston_nimi}}</b-link></td>
					<td></td>
				</tr>

				<tr v-if="request.teos_on == 'kirja'">
					<td style="width:200px">erpiv</td>
					<td>
						<DueDate :item="request"></DueDate>
					</td>
					<td @click="show_modal_loan_date = true" title="muokkaa erpiv"><b-icon icon="pen" variant="primary" id="edit-note" class="float-right cursor"></b-icon></td>
				</tr>
				<tr>
					<td style="width:200px">maksu</td>
					<td>{{request.tilaus_saatu.maksu}}</td>
					<td></td>
				</tr>
				<!--
				<tr>
					<td style="width:200px">info</td>
					<td>{{request.tilaus_saatu.info}}</td>
					<td></td>
				</tr>
			-->
				<tr>
					<td style="width:200px">tilausviesti</td>
					<td>
						<div class="cursor" @click="$root.$emit('bv::toggle::collapse', request.tilaus_saatu._id)" size="sm"><b-icon icon="caret-right" />tilausviesti</div>
						<b-collapse :id="request.tilaus_saatu._id" accordion="my-accordion" role="tabpanel">
							<div v-html="$G.renderBreaks(request.tilaus_saatu.viesti,'\n')"></div>
						</b-collapse>
					</td>
					<td></td>
				</tr>

			</table>

		</b-card>



		<br>


			<!-- TILAUKSET -->
			<!--<b-card v-if="request.tilaaja" header="tilaukset" header-tag="h5" style="border:none">-->


				<div v-if="orders.length">
					<b-table :items="orders" :fields="order_fields" class="table table-striped">

						<template v-slot:cell(kirjaston_nimi)="data">
							<div style="min-width:350px">
								<b-link :to="'/kirjastot/' + data.item.kirjasto_id"> {{data.value}}</b-link>

								<div class="cursor" @click="$root.$emit('bv::toggle::collapse', data.item._id)" size="sm"><b-icon icon="caret-right" />tilausviesti</div>

								<b-collapse :id="data.item._id" accordion="my-accordion" role="tabpanel">
									<div v-html="$G.renderBreaks(data.item.viesti,'\n')"></div>
								</b-collapse>
							</div>
						</template>

						<template v-slot:cell(pvm)="data">
							<div v-if="data.item.recdate">{{ data.item.recdate}}</div>
							<div v-else>{{ $G.getDateFromUUID(data.item._id) }}</div>
						</template>

						<template v-slot:cell(pvm_saapunut)="data">
							<div>{{formatDate(data.value)}}</div>
						</template>

						<template v-slot:cell(pvm_palautettu)="data">
							<b class="text text-info">{{formatDate(data.value)}}</b>
						</template>

						<template v-slot:cell(status)="data">
							<b class="text text-info">{{data.value.replace('_', ' ')}}</b>
						</template>

						<template v-slot:cell(toiminto)="data">

							<b-nav v-if="request.status !== 'arkistoitu'" class="float-right">
								<b-nav-item-dropdown
									v-if="data.item.status !== 'palautettu' && data.item.status !== 'ei_saatavilla'"
									id="tilaus-nav-dropdown"
									text="toiminnot"
									toggle-class="nav-link-custom"
									right
								>

			  				      <b-dropdown-divider></b-dropdown-divider>

			  				      <b-dropdown-item v-if="(request.status == 'kasittelyssa' || request.status == 'tilattu') && request.teos_on == 'kirja'" class="float-right" variant="primary" @click="openArrived(data.item)">merkitse saapuneeksi ja lainaa</b-dropdown-item>

								  <b-dropdown-item  v-if="(request.status == 'kasittelyssa' || request.status == 'tilattu') && request.teos_on == 'artikkeli'" variant="primary" class="float-right" @click="openArrived(data.item)">merkitse saapuneeksi ja arkistoi</b-dropdown-item >

			  					  <b-dropdown-item v-if="!data.item.status || data.item.status == 'tilattu'" @click="setNotAvailable(data.item._id)" >ei saatavilla</b-dropdown-item>
			  				    </b-nav-item-dropdown>
			  				  </b-nav>

						</template>
					</b-table>

				</div>

			<!--</b-card>-->


			<div class="alert alert-danger" v-if="error">{{error}}</div>



			<!-- MODAL ERPIVNVAIHTO-->
			<b-modal v-model="show_modal_loan_date"
					title="muuta erpiv"
					cancel-title="peru"
					ok-title="tallenna"
					@ok="setLoanDate"
					:header-bg-variant="primaryBG"
					:header-text-variant="primaryText">
					<div v-if="request.tilaus_saatu" class="alert alert-info">nykyinen erpiv: {{formatDate(request.tilaus_saatu.pvm_erapaiva, 'reverse','.')}}</div>
					uusi erpiv: <b-input v-model="new_loan_date"></b-input>
					<b-form-checkbox
					  id="checkbox-ei-peruutusviestia"
					  v-model="tilaus_uusittavissa"
					  name="checkbox-ei-peruutusviestia"
					  value="no"
					  unchecked-value="yes"
					  @change ="setDueDate()"
					>
					  ei voi en uusia
					</b-form-checkbox>
					<template v-if="tilaus_uusittavissa == 'no'">listieto: <b-input v-model="tilaus_info"></b-input></template>

			</b-modal>

			<!-- MODAL OTSIKKO -->
			<b-modal v-model="show_modal_title"
				title="Muokkaa otsikkoa"
				cancel-title="peru"
				ok-title="tallenna"
				@ok="updateTitle"
				:header-bg-variant="primaryBG"
				:header-text-variant="primaryText">
				<b-form-textarea v-model="title" rows="3"></b-form-textarea>
				<div v-if="sending" class="alert alert-info">lhetetn...</div>
			</b-modal>

			<!-- MODAL KARHUVIESTI -->
			<b-modal v-model="show_modal_due_notice"
				title="Lhet karhuviesti"
				cancel-title="peru"
				ok-title="lhet"
				@ok="sendDueNotice"
				:header-bg-variant="primaryBG"
				:header-text-variant="primaryText">
				karhuviesti asiakkaalle:
				<b-form-textarea v-model="due_notice" rows="6"></b-form-textarea>
				<div v-if="sending" class="alert alert-info">lhetetn...</div>
			</b-modal>

			<!-- MODAL PYYNNN PERUUTUS -->
			<b-modal v-model="show_modal_cancel_request"
				title="peruuta kaukolainapyynt"
				cancel-title="peru"
				ok-title="merkitse peruutetuksi"
				@ok="setCancelled()"
				:header-bg-variant="primaryBG"
				:header-text-variant="primaryText">
				<div class="alert alert-danger" v-if="request.status == 'lainassa'">Teos on lainassa! </div>
				<template v-if="laheta_peruutusilmoitus === 'yes'">
					peruutusviesti asiakkaalle:
					<b-form-textarea v-model="cancel_reason" rows="6"></b-form-textarea>
				</template>
				<b-form-checkbox
				  id="checkbox-ei-peruutusviestia"
				  v-model="laheta_peruutusilmoitus"
				  name="checkbox-ei-peruutusviestia"
				  value="yes"
				  unchecked-value="no"
				>
				  lhet shkposti-ilmoitus asiakkaalle
				</b-form-checkbox>
				<div v-if="sending" class="alert alert-info">lhetetn...</div>
			</b-modal>


			<!-- MODAL ARKISTOINNIN PERUUTUS-->
			<b-modal v-model="show_modal_cancel_archive"
				title="palauta ksittelyyn"
				cancel-title="peru"
				ok-title="palauta ksittelyyn"
				@ok="setNonArchived()"
				:header-bg-variant="primaryBG"
				:header-text-variant="primaryText">
				<div v-if="sending" class="alert alert-info">lhetetn...</div>
			</b-modal>

			<!-- MODAL SAAPUNEEKSI-ILMOITTAMISLOMAKE -->
			<b-modal v-model="show_modal_arrived"
				size="lg"
				title="merkitse saapuneeksi"
				cancel-title="peru"
				ok-title="merkitse saapuneeksi"
				@ok="setArrived"
				:header-bg-variant="primaryBG"
				:header-text-variant="primaryText">

				<b-form-group v-if="arrived_order" style="max-width:450px">
					<template v-if="request.teos_on == 'kirja'">
						<label>erpiv (pp.kk.vvvv)</label>
						<b-input v-model="arrived_order.erapaiva"></b-input>
					</template>

					<label>maksu</label>
					<b-input v-model="arrived_order.maksu"></b-input>

				<b-form-checkbox
				  id="checkbox-2"
				  v-model="laheta_saapumisilmoitus"
				  name="checkbox-2"
				  value="yes"
				  unchecked-value="no"
				>
				  lhet saapumisilmoitus
				</b-form-checkbox>

				</b-form-group>
				<div v-if="sending" class="alert alert-info">lhetetn...</div>
				<div class="alert alert-danger" v-if="error">{{error}}</div>
			</b-modal>

			<!-- MODAL HUOMIOLOMAKE -->
			<b-modal v-model="show_modal_note"
				size="lg"
				title="huomautuksia"
				@ok="setNote"
				@cancel="note = request.huomautuksia"
				:header-bg-variant="primaryBG"
				:header-text-variant="primaryText"
			>
				<b-form-textarea
				  id="textarea"
				  v-model="note"
				  placeholder=""
				  rows="10"
				></b-form-textarea>
				<div v-if="sending" class="alert alert-info">lhetetn...</div>

			</b-modal>

			<!-- MODAL TILAUSLOMAKE -->
			<b-modal v-model="show"
				size="lg"
				title="tilauslomake"
				:header-bg-variant="primaryBG"
				:header-text-variant="primaryText"
			>
				<WEOrderForm @exit="loadData()" v-bind:request="request"></WEOrderForm>
				<template v-slot:modal-footer>
					<div></div>
				</template>
			</b-modal>


			<!-- MODAL MAKSAJAN VALINTA -->
			<b-modal v-model="show_modal_payer"
				size="lg"
				title="maksaja"
				:header-bg-variant="primaryBG"
				:header-text-variant="primaryText"
				@ok="setPayer"
				@cancel="cancelPayer"
			>

				<b-form-checkbox
				  id="checkbox-1"
				  v-model="payer_is_client"
				  name="checkbox-1"
				  value="yes"
				  unchecked-value="no"
				>
				  asiakas maksaa itse
				</b-form-checkbox>

				<b-form v-if="payer_is_client != 'yes'">
					<b-form-group style="margin-top:30px">
						<label>valitse maksaja</label>
						<b-select
							v-model="payer_selected"
							:options="organisations"
							value-field="_id"
							text-field="nimi">
						</b-select>

						<b-form-textarea v-if="payer_selected"
						  id="textarea"
						  v-model="payer_info"
						  placeholder="Listietoa maksuun liittyen..."
						  rows="3"
						  max-rows="6"
						></b-form-textarea>
					</b-form-group>
				</b-form>
				<div v-if="sending" class="alert alert-info">lhetetn...</div>
			</b-modal>


			<!-- MODAL ULKOPUOLINEN ASIAKAS -->
			<b-modal v-model="show_modal_external" title="ulkopuolinen asiakas" @ok="saveExternalClient">
				<b-form-group style="margin-top:30px">
					<label>sukunimi</label>
					<b-input v-model="external_client.sukunimi"></b-input>
					<label>etunnimi</label>
					<b-input v-model="external_client.etunimi"></b-input>
					<label>shkposti (PAKOLLINEN)</label>
					<b-input v-model="external_client.sahkoposti"></b-input>
					<label>kortin numero</label>
					<b-input v-model="external_client.kirjastokortin_numero"></b-input>
				</b-form-group>
				<div v-if="sending" class="alert alert-info">lhetetn...</div>
			</b-modal>

			<!-- MODAL LIIT ASIAKAS -->
			<b-modal v-model="show_modal_join_client" title="liit asiakas pyyntn" cancel-title="peru" ok-title="liit" @ok="saveJoinClient">
				<b-form-group v-if="join_client" style="margin-top:30px">
					<label>sukunimi</label>
					<p><b>{{join_client.sukunimi}}</b></p>
					<label>etunnimi</label>
					<p><b>{{join_client.etunimi}}</b></p>
					<label>kortin numero</label>
					<p>{{join_client.kirjastokortin_numero}}</p>
					<label>shkposti(t)</label>
					<p><b>{{join_client.sahkoposti.join(', ')}}</b></p>
					<label>lis shkposti asiakkaalle (valinnainen)</label>
					<b-input v-model="add_sahkoposti" />


				</b-form-group>
				<div v-if="sending" class="alert alert-info">lhetetn...</div>
			</b-modal>

		</div>

		<div v-else class="alert alert-info">lataan...</div>

		<!-- ARCHIVE BANNER-->
		<img v-if="ready && (request.status == 'arkistoitu' || request.status == 'peruttu')" style="position:absolute;top: 120px; left:0%" src="@/assets/images/arkistoitu_vino.png">


	</b-container>

</template>


<script>
import axios from 'axios'
import WEOrderForm from './WEOrderForm.vue'
import DueDate from './WEDueDateDisplay.vue'
import web from '../web.js'

export default {
	name: 'WERequests',
	components: {WEOrderForm, DueDate},
	data: function() {
		return {
			request: null,
			client: null,
			client_query: '',
			title: '',
			join_client: null,
			add_sahkoposti: '',
			payer: null,
			payer_selected: null,
			payer_info: '',
			payer_is_client: 'no',
			orders: [],
			clients_result: null,
			arrived_order : null,
			laheta_saapumisilmoitus: 'yes',
			laheta_peruutusilmoitus: 'yes',
			organisations: [],
			ready: false,
			error: null,
			note:'',
			show: false,
			sending: false,
			show_modal_arrived: false,
			show_modal_payer: false,
			show_modal_loan_date: false,
			show_modal_due_notice: false,
			show_modal_external: false,
			show_modal_note: false,
			show_modal_title: false,
			show_modal_cancel_request: false,
			show_modal_cancel_archive: false,
			show_modal_join_client: false,
			cancel_reason: '',
			due_notice: '',
			new_loan_date: '',
			external_client: {},
			loan_library_name: '',
			primaryBG: 'primary',
			primaryText: 'white',
			tilaus_uusittavissa: 'yes',
			tilaus_info: '',
			fields: [
				{key:'teoksen_tiedot'},
				{key:'tilaajan_nimi'},
				{key:'tilaajan_sahkoposti', label: 'tilaajan shkposti'},
				{key: 'toiminto'}
			],
			order_fields: [
				{key: 'kirjaston_nimi'},
				{key: 'status', label: 'status'},
				{key: 'pvm', label: 'tilaus tehty'},
				{key: 'pvm_saapunut', label: 'saapunut'},
				{key: 'toiminto'}
			],
			client_fields: [
				{key: 'sukunimi'},
				{key: 'etunimi'},
				{key: 'sahkoposti', label: 'shkposti'},
				{key: 'valitse', label: 'valitse'},
			]
		}
	},
	props: ['status'],


	watch: {
		$route(to, from) {
			if(this.$route.params.id) {
				this.loadData()
			} else {
				this.current = null
			}
		}
	},

	methods: {
		openJoinClient(item) {
			this.show_modal_join_client = true
			this.join_client = item
		},

		formatDate(date, reverse, comb) {
			if(!comb) comb = '.'
			var out = []
			if(date) {
				var d = new Date(date)
				if(d instanceof Date && isFinite(d)) {
					out.push(d.getFullYear())
					out.push((d.getMonth() + 1).toString().padStart(2, "0"))
					out.push(d.getDate().toString().padStart(2, "0"))
					if(reverse) return out.reverse().join(comb)
					else return out.join(comb)
				} else {
					return date
				}
			} else {
				return '-'
			}
		},

		isExternal() {
			if(this.request.tilaajan_sahkoposti.includes('_external_') && !this.request.tilaaja) return true
			else return false
		},
		// join client by _id to request
		// add additional email to client if provided
		async saveJoinClient(bvModalEvt) {
			if(bvModalEvt) bvModalEvt.preventDefault()
			var update = {tilaaja: this.join_client._id}
			var update_client = {}

			var update_url = this.$G.api_url + "/api/pyynnot/" + this.$route.params.id
			var update_client_url = this.$G.api_url + "/api/asiakkaat/" + this.join_client._id
			try {
				var update_response = await axios.put(update_url, update)
				if(this.add_sahkoposti.includes('@')) {
					this.join_client.sahkoposti.push(this.add_sahkoposti)
					update_client.sahkoposti =  [...new Set(this.join_client.sahkoposti)]
				}
				var client_response = await axios.put(update_client_url, update_client)
				this.loadData()
				this.show_modal_join_client = false
			} catch(e) {
				alert('Asiakkaan liittminen tai shkpostin lisys ei onnistunut! ' + e )
			}

		},

		async saveExternalClient(bvModalEvt) {
			bvModalEvt.preventDefault()
			if(!this.external_client.sahkoposti || !this.external_client.sahkoposti.includes('@') || this.external_client.sahkoposti == '') {
				alert("Shkposti on pakollinen tieto")
				return
			}
			var url = this.$G.api_url + "/api/asiakkaat?sahkoposti=" + this.external_client.sahkoposti
			try {
				var search_response = await axios.get(url)
				var update = {'tilaajan_sahkoposti': this.external_client.sahkoposti}
				var update_url = this.$G.api_url + "/api/pyynnot/" + this.$route.params.id
				var update_response = await axios.put(update_url, update)
				var result = await search_response.data
				// if we did not find client, then add it
				if(result.length == 0) {
					try {
						var client_url = this.$G.api_url + "/api/asiakkaat/"
						var client_response = await axios.post(client_url, this.external_client)
						this.join_client = client_response.data
						await this.saveJoinClient()
					} catch(e) {
						alert(e)
					}
				}
				this.show_modal_external = false
				this.loadData()
			} catch(e) {
				alert(e)
				console.log(e)
			}
		},

		cancelPayer() {
			if(this.request.maksaja == 'asiakas') this.payer_is_client = 'yes'
		},

		async setPayer(e) {
			var update = {'maksaja_varmistettu': this.payer_selected, "maksaja_info": this.payer_info, 'maksaja': 'laitos'}
			// client is paying
			if(this.payer_is_client == 'yes') update = {"maksaja_varmistettu": null, "maksaja": 'asiakas'}

			try {
				var url = this.$G.api_url + "/api/pyynnot/" + this.$route.params.id
				var update_result = await axios.put(url, update);
				this.loadData()

			} catch(e) {
				alert(e)
			}
		},

		async updateTitle() {
			try {
				await web.updateRequest(this.$route.params.id, {teoksen_tiedot: this.title})
				this.loadData()
			} catch(e) {
				alert(e)
			}
		},

		setNonArchived() {
			var url = this.$G.api_url + "/api/pyynnot/"+ this.$route.params.id
			var update = {status: 'kasittelyssa'}
			axios.put(url, update)
			.then(response => {
				this.loadData()
			})
			.catch(data => {
				alert('Muutoksen tallettaminen eponnistui ' + this.getError(data,''))
			})
		},

		openArrived(order) {
			this.arrived_order = order
			this.arrived_order.erapaiva = this.$G.getToday('reverse', 1)
			this.show_modal_arrived = true
		},

		async openKarhuRequest() {
			try {
				var response = await axios(this.$G.api_url + "/api/saatteet?_id=asiakas: karhu")
				this.due_notice = response.data[0].saate
				this.due_notice = this.due_notice.replace('[[FI]]',this.$G.getInfo(this.request, 'fi'))
				this.due_notice = this.due_notice.replace('[[EN]]',this.$G.getInfo(this.request, 'en'))
				this.due_notice = this.due_notice.replace('[[ERAPAIVA]]', this.formatDate(this.request.tilaus_saatu.pvm_erapaiva, 'reverse', '.'))
				this.due_notice = this.due_notice.replace('[[ERAPAIVA]]',this.formatDate(this.request.tilaus_saatu.pvm_erapaiva, 'reverse', '.'))
			} catch(e) {
				alert('karhuviestipohjaa ei saatu')
			}

			this.show_modal_due_notice = true
		},

		openCancelRequest() {
			axios.get(this.$G.api_url + "/api/saatteet?_id=asiakas: peruutusviesti")
			.then(response => {
				this.cancel_reason = response.data[0].saate
				this.cancel_reason = this.cancel_reason.replace('[[FI]]',this.$G.getInfo(this.request, 'fi'))
				this.cancel_reason = this.cancel_reason.replace('[[EN]]',this.$G.getInfo(this.request, 'en'))
			})
			.catch(data => {this.cancel_reason = 'peruutusviestipohjaa ei lytynyt!'})
			this.show_modal_cancel_request = true
		},

		async convertToBook() {
			var update = {"teos_on": "kirja" }
			var url = this.$G.api_url + "/api/pyynnot/"+ this.$route.params.id
			axios.put(url, update)
			.then(response => {
				this.loadData()
			})
			.catch(data => {
				alert('Muunnoksen tallettaminen eponnistui ' + this.getError(data,''))
			})
		},

		setNote(bvModalEvt) {
			bvModalEvt.preventDefault()
			this.sending = true
			var update = {"huomautuksia": this.note }
			var url = this.$G.api_url + "/api/pyynnot/"+ this.$route.params.id
			axios.put(url, update)
			.then(response => {
				this.sending = false
				this.show_modal_note = false
				this.loadData()
			})
			.catch(data => {
				alert('Huomion tallettaminen eponnistui')
				this.error = data
				this.sending = false
			})
		},

		setDueDate() {
			if(this.new_loan_date == '') this.new_loan_date = this.formatDate(this.request.tilaus_saatu.pvm_erapaiva, 'reverse','.')
		},

		async sendDueNotice(bvModalEvt) {
			bvModalEvt.preventDefault()
			this.sending = true;
			var update = {"notice": this.due_notice }
			var url = this.$G.api_url + "/api/pyynnot/"+ this.$route.params.id + "/karhu"
			try {
				var response = await axios.post(url, update)
				this.sending = false
				this.show_modal_due_notice = false
				this.loadData()
			} catch(e) {
				this.sending = false
				var msg = ''
				if(e.response.data && e.response.data.error) msg = e.response.data.error
				alert(msg)
			}
		},

		setCancelled() {
			this.sending = true
			var update = {"peruutuksen_syy": this.cancel_reason ,"laheta_peruutusilmoitus": this.laheta_peruutusilmoitus}
			var url = this.$G.api_url + "/api/pyynnot/"+ this.$route.params.id + "/cancelled"
			axios.put(url, update)
			.then(response => {
				this.sending = false
				this.loadData()
			})
			.catch(data => {
				alert('peruutus eponnistui')
				this.sending = false
				this.error = data
			})
		},

		setLoanDate(bvModalEvt) {
			this.sending = true
			bvModalEvt.preventDefault()
			var update = {
				"due_date": this.new_loan_date.split('.').reverse().join('-'),
				"info": this.tilaus_info,
				"uusittavissa": this.tilaus_uusittavissa
			 }
			var url = this.$G.api_url + "/api/pyynnot/"+ this.$route.params.id + "/due_date"
			axios.put(url, update)
			.then(response => {
				this.sending = false
				this.show_modal_loan_date = false
				this.loadData()
			})
			.catch(data => {
				this.sending = false
				alert(this.getError(data, 'erpivn asettaminen eponnistui'))
			})
		},

		setReturned() {
			this.sending = true
			var update = {"pvm_palautettu": this.$G.getToday(), "status": 'palautettu'}
			var url = this.$G.api_url + "/api/pyynnot/" + this.$route.params.id + "/returned"

			axios.put(url, update)
			.then(response => {
				this.sending = false
				this.loadData()
			})
			.catch(data => {
				this.sending = false
				alert(this.getError(data, 'Arkistointi eponnistui'))
			})
		},

		setNotAvailable(order_id) {
			this.sending = false
			// update order
			var resp = null

			var url = this.$G.api_url + "/api/pyynnot/" + this.$route.params.id + "/tilaukset/"+ order_id + "/not_available";
			axios.put(url, {})
			.then(response => {
				this.sending = false
				this.loadData()
			})
			.catch(data => {
				this.sending = false
				//this.error = this.getError(data, 'Pyynt eponnistui')
				alert(this.getError(data, 'Pyynt eponnistui'))
			})
		},

		setArrived(bvModalEvt) {
			bvModalEvt.preventDefault()
			this.sending = false
			// update order
			var resp = null
			var update = {
				"erapaiva": this.arrived_order.erapaiva.split('.').reverse().join('-'),
				"maksu": this.arrived_order.maksu,
				"laheta_saapumisilmoitus": this.laheta_saapumisilmoitus
			}
			var url = this.$G.api_url + "/api/pyynnot/" + this.$route.params.id + "/tilaukset/"+ this.arrived_order._id + "/arrived";
			axios.put(url, update)
			.then(response => {
				this.sending = false
				this.show_modal_arrived = false
				this.loadData()
			})
			.catch(data => {
				this.sending = false
				//this.error = this.getError(data, 'Pyynt eponnistui')
				alert(this.getError(data, 'Pyynt eponnistui'))
			})
		},

		checkDate(date) {
			// expecting dd.mm.YYYY
			var splitted = date.split('.')
			if(splitted.length !== 3) return false
		},
		async reserve(id) {
			var url = this.$G.api_url + "/api/pyynnot/"+ id;
			var result = await axios.get(url);
			var item = result.data
			if(item && (item['status'] == '' || item['status'] == 'uusi')) {
				var update = {status: 'kasittelyssa', "kasittelija": this.$G.user}
				var update_result = await axios.put(url, update);
			}
		},

		setLoanLibrary() {
			for(var order of this.orders) {
				if(order.pvm_saapunut) this.loan_library_name = order.kirjaston_nimi
			}
		},

		async searchClients() {
			var url = this.$G.api_url + "/api"
			var r = await axios.get(url + "/asiakkaat/?search=" + this.client_query)
			this.clients_result = r.data
		},

		async loadData() {
			this.show = false
			this.error = null
			this.arrived_order = null
			this.ready = false
			this.payer_selected = null
			this.payer_is_client = 'no'
			try {
				var url = this.$G.api_url + "/api"
				const [r_request, r_organisations] = await Promise.all([
					axios.get(url + "/pyynnot/" + this.$route.params.id),
					axios.get(url + "/organisaatiot")
				]);

				this.request = r_request.data
				if(this.request.tilaus_saatu) {
					this.tilaus_uusittavissa = this.request.tilaus_saatu.uusittavissa
					this.tilaus_info = this.request.tilaus_saatu.info
				}

				if(!this.request.tilaajan_sahkoposti) {
					this.request.tilaajan_sahkoposti = 'VIRHEELLINEN TILAAJA'
				}

				this.title = this.request.teoksen_tiedot
				this.orders = this.request.tilaukset
				this.note = this.request.huomautuksia
				this.organisations = r_organisations.data
				if(this.request.maksaja_varmistettu && this.request.maksaja_varmistettu._id)
					this.payer_selected =  this.request.maksaja_varmistettu._id
				if(this.request.maksaja === 'asiakas') this.payer_is_client = 'yes'
				this.setLoanLibrary()

				// external client
				if(this.request.tilaajan_sahkoposti.includes("_external_")) {
					this.external_client.sukunimi = this.request.sukunimi
					this.external_client.etunimi = this.request.etunimi
					this.external_client.kirjastokortin_numero = this.request.kirjastokortin_numero
					this.client_query = this.request.sukunimi
					this.searchClients()
				}

				this.ready = true
				if(this.request.status === 'uusi') {
					this.reserve(this.request._id)
				}
			} catch(e) {
				alert(e)
				this.error = "virhe"
				this.ready = true
			}
		},
		getError(e, msg) {
			if(e.response && e.response.data && e.response.data.error) return e.response.data.error
			else return msg
		}
	},

	mounted() {
		this.loadData()
	}
}
</script>
