<template >
	<div>

		<b-form-group>
			<label>valitse kirjasto</label>
			<b-select id="lib_select"
				v-model="selected"
				:options="libraries"
				value-field="_id"
				text-field="nimi">
			</b-select>


			<b-container v-if="current">

				<h3>{{current.nimi}}</h3>
				<b-card>
					<table>
						<tr v-for="field in fields" :key="field.key">

							<td style="min-width:200px">{{field.key}}</td>
							<td v-if="field.key == 'lomake' || field.key == 'kotisivut'"><a target="_blank" :href="current[field.key]">{{current[field.key]}}</a></td>
							<td v-else><b>{{current[field.key]}}</b></td>
						</tr>
					</table>

				</b-card>

				<div v-if="!current.lomake">

					<label>saatepohjat</label>
					<b-select id="saate_select"
						:options="saatteet"
						value-field="_id"
						text-field="_id"
						v-model="message_id">
					</b-select>

					<label>saateviesti</label>
					<b-form-textarea
					  id="textarea"
					  v-model="message"
					  placeholder="valitse saatepohja..."
					  rows="10"
					></b-form-textarea>
				</div>

				<div v-if="current.lomake && current.lomake != ''">
					<div class="alert alert-info"><a target="_blank" :href="current.lomake">tilauslomake</a> (aukeaa uuteen ikkunaan)</div><br>

				</div>
				<!--
				<label>listieto (ei lhetet)</label>
				<b-form-textarea v-if="current"
				  id="textarea"
				  v-model="info"
				  placeholder="Listietoa tilaukseen liittyen..."
				  rows="3"
				  max-rows="3"
				></b-form-textarea>
				-->

				<b-button id="send-order" variant="primary" @click="sendOrder()" class="float-right" style="margin-top:20px">lhet tilaus</b-button>
				<div v-if="sending" class="alert alert-info">Lhetetn...</div>
				<b-form-checkbox
				  id="checkbox-2"
				  v-model="send_email"
				  name="checkbox-2"
				  value="no"
				  unchecked-value="yes"
				>
				  l lhet tilausta shkpostitse
				</b-form-checkbox>
			</b-container>

		</b-form-group>

		<div class="alert alert-danger" v-if="error">{{error}}</div>

	</div>

</template>


<script>

export default {
	name: 'WEOrderForm',
	data: function() {
		return {
			libraries: [],
			message: '',
			message_id: null,
			info: '',
			selected: null,
			current: null,
			error: null,
			fields: [
				{key: 'email', label:'shkposti'},
				{key: 'osoite'},
				{key: 'kotisivut'}
			],
			saatteet: [],
			send_email: 'yes',
			sending: false
		}
	},

	props: ['request'],

	watch: {
		selected() {
			this.getCurrent()
		},
		message_id() {
			for(var m of this.saatteet) {
				if(m._id == this.message_id) {
					this.message = m.saate
					var info_en = this.getInfo('en')
					var info_fi = this.getInfo('fi')
					this.message = this.message.replace('[[ID]]', this.request._id)
					if(this.request.tarvitaan_viimeistaan && this.request.tarvitaan_viimeistaan != 'undefined')
						this.message = this.message.replace('[[TARVITAAN_VIIMEISTAAN]]', this.request.tarvitaan_viimeistaan)
					this.message = this.message.replace('[[EN]]', info_en)
					this.message = this.message.replace('[[FI]]', info_fi)
				}
			}

		}
	},
	methods: {
		getCurrent() {
			for(var lib of this.libraries) {
				if(lib._id == this.selected) {
					this.current = lib
					this.message = lib.saate
				}
			}
		},

		getInfo(lang) {
			var lang_info = {
				'pyynto_id': {'en':'ID', 'fi': 'ID'},
				'teoksen_tiedot': {'en':'title', 'fi': 'teos'}
			}

			return lang_info['teoksen_tiedot'][lang] + ': ' + this.request.teoksen_tiedot

		},

		async sendOrder() {
			if(!this.message) {
				alert('Saateviesti on pakollinen!')
				return
			}

			this.sending = true;
			var order = {
				'kirjasto_id' : this.current._id,
				'kirjaston_nimi': this.current.nimi,
				'viesti': this.message,
				'info': this.info,
				'message_id': this.message_id,
				'send_mail': this.send_email
			}

			try {
				var response = await fetch(this.$G.api_url + "/api/pyynnot/" + this.$route.params.id + "/tilaukset", {method:"POST", body: JSON.stringify(order)})
				var json = await response.json()
				if(!response.ok) throw('fail')
				else {
					this.sending = false
					this.$emit('exit', true)
				}
			} catch(e) {
				this.sending = false
				if(json && json.error) alert(json.error)
				else alert('virhe ' + e)
			}

		},

		loadMessages() {
			fetch(this.$G.api_url + "/api/saatteet?_id=tilaussaate*")
			.then(response => { return response.json()})
			.then(json => {
				this.saatteet = json
				this.ready = true
			})
			.catch(data => {console.log('error' + data)})
		},

		loadData() {
			this.ready = false
			fetch(this.$G.api_url + "/api/kirjastot?sort=nimi")
			.then(response => { return response.json()})
			.then(json => {
				this.libraries = json
				this.loadMessages()
				this.ready = true
			})
			.catch(data => {alert('error' + data)})
		}
	},

	mounted() {
		this.loadData()

	}
}
</script>
