import axios from "axios"
let web = {}

const api = '/api'

web.updateRequest = async function(request_id, update) {

	var url = api + "/api/pyynnot/" + request_id ;
	axios.put(url, update)
	.catch(data => {
		throw(getError(data, 'Pyynt eponnistui'))
	})
}

web.getLoanCount = async function() {
	var url = api + "/api/pyynnot/loan_count" ;
	return axios.get(url)

	
}

function getError(e, msg) {
	if(e.response && e.response.data && e.response.data.error) return e.response.data.error
	else return msg
}

export default web
