<style>
.pointer {
	cursor: pointer;
}
.nav-weskari {
	border-bottom: 0.1mm solid #002957;
	background-color: #002957;
	margin-bottom:20px;
}
.brand {
	margin-left:10px
}
</style>
<template>


  <div id="app">


		<div>
		  <b-navbar toggleable="lg" type="dark" class="nav-weskari">
			<img class="mr-3" src="@/assets/images/logo.svg" height="40px">
			<b-navbar-brand :to="{ path: '/'}" class="brand">WESKARI </b-navbar-brand>

			<b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

			<b-collapse id="nav-collapse" is-nav>
			  <b-navbar-nav>
				<b-nav-item :to="'/lainat'">Lainat</b-nav-item>
				<b-nav-item :to="'/asiakkaat'">Asiakkaat</b-nav-item>
				<b-nav-item :to="'/kirjastot'">Kirjastot</b-nav-item>
				<b-nav-item :to="'/organisaatiot'">Organisaatiot</b-nav-item>
				<b-nav-item :to="'/saatteet'">Saatteet</b-nav-item>
				<b-nav-item :to="'/tilastot'">Tilastot</b-nav-item>
			  </b-navbar-nav>
			  <!-- Right aligned nav items -->
			 <!-- <b-badge class="alert alert-danger float-right">testiversio!</b-badge>-->
			  <b-navbar-nav class="ml-auto">


				<b-nav-item-dropdown right>
				  <!-- Using 'button-content' slot -->
				  <template v-slot:button-content>
					<em>{{$G.user}}</em>
				  </template>
				  <b-dropdown-item href="/Shibboleth.sso/Logout">kirjaudu ulos</b-dropdown-item>
				</b-nav-item-dropdown>
			  </b-navbar-nav>
			</b-collapse>
		  </b-navbar>

		</div>




		<!-- ROUTE VIEW (main) -->
		<b-container fluid v-if="$G.login" >
			<keep-alive include="WELoans,WELibrary,WEClient">
				<router-view class="view" v-if="!error"></router-view>
			</keep-alive>
			<div class="alert alert-danger" v-if="error">{{error}}</div>
		</b-container>



  </div>
</template>


<script>


export default {
	name: 'app',
	data: function() {
		return {
			error: null,
			user: null,
			logging: false,
			intervalID: null
		}
	},
	methods: {
		login: function() {

			console.log('trying login...')
			fetch(this.$G.api_url + "/api/auth", {credentials: "include"})
			.then(response => {
				if(!response.ok) throw({msg: 'session terminated'})
				return response
			})
			.then(response => response.json())
			.then(data => {
				this.$G.user = data.shibboleth.user
			})
			.catch(e => {
				console.log(e)
				if(e.msg && e.msg == 'session terminated')
					this.error = 'Kirjautuminen vanheni. Lataa sivu uudestaan.'
				else
					this.error = 'Kirjautuminen eponnistui. Kirjautuminen luultavasti vanheni, lataa sivu uudestaan.'
				this.$G.user = ''
			})
		}
	},
    watch: {
        $route: {
            immediate: true,
            handler(to, from) {
                document.title = to.meta.title || 'WESKARI';
            }
        },
    },
	created() {
		document.title = 'WESKARI'
		console.log(window.location.hostname)
		if(window.location.hostname == 'localhost') this.$G.api_url = '' // set api path dynamically
		this.login()
		this.intervalID = setInterval(() => this.login(), 30000);
	},
	beforeDestroy () {
		clearInterval(this.intervalID)
	}

}
</script>
