import Vue from 'vue'
import VueRouter from 'vue-router'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
Vue.use(BootstrapVue)
Vue.use(IconsPlugin)
Vue.use(VueRouter)

// 2. Define route components
import WEMain from './components/WEMain.vue'
import WERequest from './components/WERequest.vue'
import WEOrganisation from './components/WEOrganisation.vue'
import WEClient from './components/WEClient.vue'
import WELibrary from './components/WELibrary.vue'
import WEMessage from './components/WEMessage.vue'
import WELoanSearch from './components/WELoans.vue'
import WEStatistics from './components/WEStatistics.vue'


// 3. Create the router
const router = new VueRouter({
  //mode: 'history',
  base: __dirname,
  routes: [
    { path: '/', component: WEMain },
    { path: '/pyynnot/:id', component: WERequest },
    { path: '/organisaatiot', component: WEOrganisation },
    { path: '/organisaatiot/:id', component: WEOrganisation },
    { path: '/asiakkaat', component: WEClient },
    { path: '/asiakkaat/:id', component: WEClient },
    { path: '/kirjastot', component: WELibrary },
    { path: '/kirjastot/:id', component: WELibrary },
    { path: '/saatteet', component: WEMessage },
    { path: '/saatteet/:id', component: WEMessage },
    { path: '/lainat', component: WELoanSearch },
    { path: '/tilastot', component: WEStatistics },
  ]
})

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'


import App from './App.vue'

Vue.config.productionTip = false


// instance property https://vuejs.org/v2/cookbook/adding-instance-properties.html
var shared = new Vue({
	data:{
		api_url: '/api',
		login: true ,
		error: null,
		user: null
	},
	methods: {
		getStatus(status) {
			if(status == 'kasittelyssa') return 'ksittelyss'
			else return status
		},

		isOverdue(d) {
			var dd = new Date(d)
			var now = new Date()
			if(dd.getTime() < now.getTime()) return true
			return false
		},

		formatDate(date, reverse, comb) {
			if(!comb) comb = '.'
			var out = []
			if(date) {
				var d = new Date(date)
				if(d instanceof Date && isFinite(d)) {
					out.push(d.getFullYear())
					out.push((d.getMonth() + 1).toString().padStart(2, "0"))
					out.push(d.getDate().toString().padStart(2, "0"))
					if(reverse) return out.reverse().join(comb)
					else return out.join(comb)
				} else {
					return date
				}
			} else {
				return '-'
			}
		},

		getDateFromUUID(uuid) {
			var date = new Date( parseInt( uuid.substring(0,8), 16 ) * 1000 )
			var formatted = new Intl.DateTimeFormat('fi-FI').format(date);
			return formatted
		},

		getToday(reverse, plusMonth, separator='.') {
			var today = new Date();
			if(plusMonth) {
				today.setMonth(today.getMonth() + plusMonth)
			}
			if(reverse)
				return (('0' + (today.getDate())).slice(-2) + separator + ('0' + (today.getMonth()+1)).slice(-2) + separator + today.getFullYear());
			else
				return (today.getFullYear() + separator + ('0' + (today.getMonth()+1)).slice(-2) + separator + ('0' + (today.getDate())).slice(-2));
		},

		renderBreaks(text, splitter) {
			if(text && typeof text === 'string') return text.split(splitter).join('<br>');
			else return '';
		},

		getInfo(request, lang) {
			var lang_info = {
				'pyynto_id': {'en':'ID', 'fi': 'ID'},
				'teoksen_tiedot': {'en':'title', 'fi': 'teos'},
				'sivut': {'en':'pages', 'fi': 'sivut'},
				'julkaisun_tiedot': {'en': 'publication', 'fi': 'julkaisu'}
			}
			return lang_info['teoksen_tiedot'][lang] + ': ' + request.teoksen_tiedot

		},

	},
	computed: {
		local: function() {
			if(window.location.hostname == 'localhost')
				return ''
			else
				return this.api_url
		}
	}
})

shared.install = function(){
  Object.defineProperty(Vue.prototype, '$G', {
    get () { return shared }
  })
}
Vue.use(shared);


new Vue({
	router,
	render: h => h(App),
}).$mount('#app')
