<style scoped>
	table {
		margin-bottom:60px;
	}
</style>

<template >
	<b-container fluid>
		<h1>Pyynnt</h1>
		<div class="alert- alert-danger" v-if="error">{{error}}</div>
		<div>
			<h4>uudet</h4>
			<template v-if="ready['uusi']">
				<b-table small striped hover :items="new_requests" :fields="fields" >
					<template v-slot:cell(tiedot)="data">
						<b-link :to="{ path: '/pyynnot/' + data.item._id }">{{data.item.teoksen_tiedot}}</b-link>
					</template>
					<template v-slot:cell(tilaaja)="data">
						<b-link v-if="data.item.tilaaja" :to="{ path: '/asiakkaat/' + data.item.tilaaja._id }">{{ data.item.tilaaja.sukunimi + ', ' + data.item.tilaaja.etunimi }}</b-link>
						<div v-else>{{data.item.sukunimi}}, {{data.item.etunimi}} ulkopuolinen</div>
					</template>
					<template v-slot:cell(sahkoposti)="data">
						<div v-if="data.item.tilaaja">{{ data.item.tilaaja.sahkoposti.join(', ')}}</div>
						<div v-else class="alert alert-warning">ulkopuolinen</div>
					</template>
				</b-table>
			</template>
			<div class="alert alert-info" v-else>ei uusia pyyntj</div>

		</div>


		<h4>ksittelyss</h4>
		<template v-if="ready['kasittelyssa']">
			<b-table small striped hover :items="reserved_requests" :fields="fields" >

				<template v-slot:cell(tiedot)="data">
					<b-link :to="{ path: '/pyynnot/' + data.item._id }">{{data.item.teoksen_tiedot}}</b-link>
				</template>
				<template v-slot:cell(tilaaja)="data">
					<b-link v-if="data.item.tilaaja" :to="{ path: '/asiakkaat/' + data.item.tilaaja._id }">{{ data.item.tilaaja.sukunimi + ', ' + data.item.tilaaja.etunimi }}</b-link>
					<div v-else>{{data.item.sukunimi}}, {{data.item.etunimi}} ulkopuolinen</div>
				</template>
				<template v-slot:cell(sahkoposti)="data">
					<div v-if="data.item.tilaaja">{{ data.item.tilaaja.sahkoposti.join(', ')}}</div>
					<div class="alert alert-warning" v-else>ulkopuolinen</div>
				</template>
			</b-table>
		</template>
		<div class="alert alert-info" v-else>ei ksittelyss olevia pyyntj</div>


		<h4>tilattu</h4>
		<template v-if="ready['tilattu']">
			<b-table small striped hover :items="ordered_requests" :fields="fields" >

				<template v-slot:cell(tiedot)="data">
					<b-link :to="{ path: '/pyynnot/' + data.item._id }">{{data.item.teoksen_tiedot}}</b-link>
				</template>
				<template v-slot:cell(tilaaja)="data">
					<b-link v-if="data.item.tilaaja" :to="{ path: '/asiakkaat/' + data.item.tilaaja._id }">{{ data.item.tilaaja.sukunimi + ', ' + data.item.tilaaja.etunimi }}</b-link>
				</template>
				<template v-slot:cell(sahkoposti)="data">
					<div v-if="data.item.tilaaja">{{ data.item.tilaaja.sahkoposti.join(', ')}}</div>
				</template>
			</b-table>
		</template>
		<div class="alert alert-info" v-else>ei saapumattomia tilauksia</div>




	</b-container>

</template>


<script>
import axios from 'axios'

export default {
	name: 'WERequests',
	data: function() {
		return {
			new_requests: [],
			reserved_requests: [],
			ordered_requests: [],
			current: null,
			ready: {tilattu: false, kasittelyssa: false, uusi: false},
			reserved_ready: false,
			error: null,
			fields: [
				{key:'_id', label: 'ID', thStyle:'width:10em'},
				{key:'teos_on', label: 'tyyppi', thStyle:'width:6em'},
				{key:'tiedot', thStyle:'min-width:50%'},
				//{key:'sahkoposti', label: 'shkposti'},
				{key:'maksaja', label:'maksaja'},
				{key:'tilaaja', label: 'asiakas'},
				{key:'kasittelija', label: 'ksittelij'}
			]
		}
	},
	props: ['status'],

	watch: {
		current: function() {
			this.$G.current_request = this.current
		}
	},

	methods: {
		loadData(status, target, query) {

			axios.get(this.$G.api_url + "/api/pyynnot?status=" + status + query)
			.then(response => {
				this[target] = response.data
				if(this[target].length)
					this.ready[status] = true
				else
					this.ready[status] = false
			})
			.catch(data => {
				console.log('error' + data.response)
				this.error = 'virhe: ' + data.response.data.error
			})
		}
	},

	mounted() {
		this.loadData('uusi', 'new_requests', '')
		this.loadData('kasittelyssa', 'reserved_requests', '')
		this.loadData('tilattu', 'ordered_requests', '')
	}
}
</script>
