<template >
	<b-container fluid>
		<h1>Kirjastot</h1>


		<!-- display search -->
		<b-card title="haku" v-if="!current && mode == ''">
		  <b-row class="my-1">
			<b-col sm="4">
			  <b-form-input @keydown.enter.native="search" v-model="search_term" id="input-default" placeholder="hae nimen alkukirjaimilla"></b-form-input>
			  <label>TAI </label>
			  <b-form-input @keydown.enter.native="search" v-model="search_lyhenne" id="input-default2" placeholder="hae lyhenteell"></b-form-input>
			  <b-button @click="search">hae</b-button>
			</b-col>
		  </b-row>
		</b-card>
		<br>
		<b-button class="float-right" v-if="!current && mode == ''" @click="mode = 'add_new'" variant="primary">lis uusi</b-button>

		<div v-if="loading" class="alert alert-info">haen...</div>

		<!-- display search result -->
		<div v-if="search_result.length !== 0 && !current && mode == ''">
				<b-table :items="search_result" :fields="fields_search">
					<template v-slot:cell(sahkoposti)="data">
						<b class="text-info">{{ data.value }}</b>
					</template>
					<template v-slot:cell(nimi)="data">
							<b-link :to="'/kirjastot/' + data.item._id">{{data.value}}</b-link>
					</template>
				</b-table>
		</div>


		<!-- display individual item -->
		<b-card v-if="current && mode != 'edit'" :title="current.nimi + ' (' + current.lyhenne + ')'" header-bg-variant="dark">

			<table class="table table-sm">
				<tr v-for="field in fields" :key="field.key">

					<td style="min-width:200px">{{field.key}}</td>
					<td v-if="field.key == 'lomake' || field.key == 'kotisivut'"><a target="_blank" :href="getLink(current[field.key])">{{current[field.key]}}</a></td>
					<td v-else><b>{{current[field.key]}}</b></td>
				</tr>
			</table>
			<b-icon v-if="mode != 'edit'" @click="mode = 'edit'" icon="pen" class="float-right pointer" variant="primary">muokkaa</b-icon>

		</b-card>


		<!-- edit form -->
		<b-card v-if="current && mode == 'edit'" :title="current.nimi" header-bg-variant="dark">
			<table class="table table-striped">
				<tr v-for="field in fields" :key="field.key">
					<template v-if="field.key != '_id'">
						<td v-if="field.label" style="min-width:200px">{{field.label}}</td>
						<td v-else style="min-width:200px">{{field.key}}</td>
						<td style="min-width:350px" ><b-form-input v-model="current[field.key]"></b-form-input></td>
					</template>
				</tr>
			</table>

			<b-container>

				<b-button  @click="saveForm()" class="float-right" variant="primary" style="margin-left:20px">tallenna</b-button>
				<b-button  @click="loadData()" class="float-right" >peru</b-button>

			</b-container>
		</b-card>

		<b-link v-if="current" :to="'/kirjastot'">takaisin hakuun</b-link>


		<!-- add new item-->
		<b-card v-if="mode == 'add_new'" title="uusi kirjasto" header-bg-variant="dark">
			<table>
				<tr v-for="field in fields" :key="field.key">
					<td v-if="field.label" style="min-width:200px">{{field.label}}</td>
					<td v-else style="min-width:200px">{{field.key}}</td>
					<td style="min-width:350px" ><b-form-input v-model="new_obj[field.key]"></b-form-input></td>
				</tr>
			</table>
			<b-button @click="mode = ''">peru</b-button>
			<b-button @click="saveForm()" variant="info">tallenna</b-button>
		</b-card>

		<div class="alert alert-danger" v-if="error">{{error}}</div>

		<br><br><h2 v-if="current">lainassa olevat</h2>
		<b-card v-if="current">
			<b-table :items="requests" :fields="request_fields">
				<template v-slot:cell(tiedot)="data">
						<b-link :to="'/pyynnot/' + data.item._id">{{data.item.teoksen_tiedot}}</b-link>
				</template>
				<template v-slot:cell(tilaaja)="data">
					<b-link v-if="data.item.tilaaja" :to="{ path: '/asiakkaat/' + data.item.tilaaja._id }">{{ data.item.tilaaja.sukunimi}}, {{data.item.tilaaja.etunimi}}</b-link>
				</template>
				<template v-slot:cell(erapaiva)="data">
						<DueDate :item="data.item"></DueDate>
				</template>
			</b-table>


		</b-card>

		<h2 v-if="current">arkistoidut</h2>
		<b-card v-if="current">

			<b-button @click="loadArchive()">nyt arkistoidut</b-button>
			<b-table :items="archived" :fields="request_fields">
				<template v-slot:cell(tiedot)="data">
						<b-link :to="'/pyynnot/' + data.item._id">{{data.item.teoksen_tiedot}}</b-link>
				</template>
				<template v-slot:cell(tilaaja)="data">
					<b-link v-if="data.item.tilaaja" :to="{ path: '/asiakkaat/' + data.item.tilaaja._id }">{{ data.item.tilaaja.sukunimi}}, {{data.item.tilaaja.etunimi}}</b-link>
				</template>
				<template v-slot:cell(erapaiva)="data">
						<DueDate :item="data.item"></DueDate>
				</template>
			</b-table>

	</b-container>

</template>


<script>
import DueDate from './WEDueDateDisplay.vue'
export default {
	name: 'WELibrary',
	components: {DueDate},
	data: function() {
		return {
			search_term: '',
			search_lyhenne: '',
			search_result: [],
			archived: [],
			current: null,
			requests: [],
			ready: false,
			loading: false,
			error: null,
			mode: '',
			new_obj: {},
			fields_search: [
				{key: 'nimi'},
				{key: 'lyhenne'},
				{key: 'email', label: 'shkposti'},
				{key: 'puhelin'}
			],
			fields: [
				{key: 'nimi'},
				{key: 'puhelin'},
				{key: 'lyhenne'},
				{key: 'email', label: 'shkposti'},
				{key: 'osoite'},
				{key: 'huomio'},
				{key: 'lomake', label: 'tilauslomakkeen URL'},
				{key: 'kotisivut', label: 'kotisivujen URL'}
			],
			request_fields: [
				{key: '_id', label: 'tunniste'},
				{key: 'tiedot'},
				{key: 'status'},
				{key: 'tilaaja', label: 'asiakas'},
				{key: 'erapaiva', label: 'erpiv'}
			]
		}
	},

	watch: {
		$route(to, from) {
			if(to.path.includes('/kirjastot')) {
				if(this.$route.params.id) {
					this.loadData()
					// if we hit back button *while in* library view, then we reset current library
				} else if(from.path.includes('/kirjastot')) {
					this.current = null
					// if we came from somewhere else with current set, then re-route so that URL matches current id
				} else {
					if(this.current) this.$router.replace({path:'/kirjastot/' + this.current._id})
				}
			}
		}
	},

	methods: {

		getLink(value) {
			if(value) {
				if(value.includes('http')) return value
				else return 'http://' + value
			}
		},

		saveForm() {
			console.log('saveform')
			if(this.mode == 'add_new') {
				if(!this.new_obj.nimi || this.new_obj.nimi == '') {alert('Nimi tytyy antaa'); return}
				fetch(this.$G.api_url + "/api/kirjastot", {method:'POST', body: JSON.stringify(this.new_obj)})
				.then(response => { return response.json()})
				.then(json => {
					this.$router.push("/kirjastot/" + json._id)
				})
				.catch(data => {console.log('error' + data)})
			} else if(this.mode == 'edit') {
				delete this.current._id
				fetch(this.$G.api_url + "/api/kirjastot/" + this.$route.params.id, {method:'PUT', body: JSON.stringify(this.current)})
				.then(response => { return response.json()})
				.then(json => {
					this.loadData()
				})
				.catch(data => {console.log('error' + data)})
			}
		},

		editForm() {
			console.log(this.edit)
			this.edit = !this.edit
		},

		loadData() {
			if(this.$route.params.id) {
				this.mode = ''
				this.error = null
				this.ready = false
				this.loading = true
				this.current = null
				this.archived = []
				fetch(this.$G.api_url + "/api/kirjastot/" + this.$route.params.id)
				.then(response => { return response.json()})
				.then(json => {
					this.current = json
					this.loadRelated()
				})
				.catch(data => {
					this.error = data
					console.log('error' + data)
				})
			}
		},

		loadRelated() {
			fetch(this.$G.api_url + "/api/kirjastot/" + this.$route.params.id + "/loans")
			.then(response => { return response.json()})
			.then(json => {
				this.requests = json
				this.ready = true
				this.loading = false
			})
			.catch(data => {console.log('error' + data)})
		},

		async loadArchive() {
			var result = await fetch(this.$G.api_url + "/api/kirjastot/" + this.$route.params.id + "/loans?status=arkistoitu")
				this.archived = await result.json()
		},

		async search() {
			this.current = null
			this.error = null
			this.loading = true
			var query = ''
			if(this.search_term !== '') {
				if(!this.search_term.includes('*')) this.search_term = this.search_term + '*'
				query = 'nimi=' + this.search_term

				var result1 = await fetch(this.$G.api_url + "/api/kirjastot?" + query)
				this.search_result = await result1.json()
				this.loading = false
			} else if(this.search_lyhenne !== '') {
				query = 'lyhenne=' + this.search_lyhenne
				var result = await fetch(this.$G.api_url + "/api/kirjastot?" + query)
				this.search_result = await result.json()
				this.loading = false
			}
		}
	},

	mounted() {
		this.loadData()
	}
}
</script>
